@import url(https://fonts.googleapis.com/css?family=Montserrat:400,500,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:600&display=swap);
body {
  margin: 0;
  font-family:'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-width: 325px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

button{
  outline: none !important;
  box-shadow: none !important;
}
.form-setting{
    min-height: 90vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    background-color: #f1f2f6;
}

.login-card{
    background-color: white;
    min-height: 50vh;
    padding: 35px 55px 35px 55px !important;
}

.link-blue{
    cursor: pointer;
    color: #3742fa;
    font-weight: 400;
    font-family: 'Roboto', sans-serif;
    font-size: 16px;
    text-decoration: none !important;
}

.link-blue:hover{
    color: #5352ed;
    text-decoration: none !important;
}

.btn-google{
    background-color: white !important;
    margin-top: 28px;
    box-shadow: 1px 1px 6px 2px rgba(0, 0, 0, 0.05), 1px 2px 10px 2px rgba(0, 0, 0, 0.04 ) !important;
    font-family: 'Open Sans', sans-serif;
    font-size: 14px;
    font-weight: 500;
}

.btn-google:hover{
    box-shadow: 1px 1px 8px 2px rgba(0, 0, 0, 0.05), 1px 2px 10px 2px rgba(0, 0, 0, 0.1 ) !important;
}

.btn-signin{
    background-color: #c0392b !important;
    margin-top: 12px;
    font-family: 'Open Sans', sans-serif;
    font-size: 14px;
    font-weight: 500;
    color:white !important;
}

.btn-signin:hover{
    color: white;
    background-color: #e74c3c !important;
}

@media only screen and (max-width:992px) {
    .form-setting{
        min-height: auto;
        justify-content: flex-start;
    }
}
.signup-form{
min-height: 100vh;
display: flex;
flex-direction: column;
align-items:center;
justify-content: center;
}

.btn-register{
    background-color: #c0392b !important;
    margin-top: 12px;
    font-family: 'Open Sans', sans-serif;
    font-size: 20px;
    font-weight: 500;
    color:white !important;
    padding:10px 10px 10px 10px !important;
}

.btn-register:hover{
    color: white;
    background-color: #e74c3c !important;
}

.btn-signup{
    background-color: white !important;
    margin-top: 12px;
    box-shadow: 1px 1px 8px 2px rgba(0, 0, 0, 0.075), 1px 2px 20px 2px rgba(0, 0, 0, 0.05 );
    font-family: 'Open Sans', sans-serif;
    font-size: 14px;
    font-weight: 500;
}

.btn-signup:hover{
    background-color: #ffffff !important;
    box-shadow: 1px 4px 8px 2px rgba(0, 0, 0, 0.08), 1px 6px 20px 2px rgba(0, 0, 0, 0.05 );
}

@media only screen and (max-width:992px){
    .sp-form{
        min-height: auto;
        margin-top: 30px;
    }
}
.navbar{
    min-height: 10vh;
    background-color: white;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    box-shadow: 1px 2px 4px 2px rgba(0, 0, 0, 0.08), 1px 3px 10px 2px rgba(0, 0, 0, 0.05 );
}

.link-nav{
    font-size: 16px;
    color: grey;
    font-weight: 500;
    text-decoration: none !important;
}

.link-nav:hover{
    color: #c0392b;
    font-weight: 500;
}

.link-nav-active {
     font-size: 16px;
    color:  #c0392b;
    font-weight: 600;
    text-decoration: none !important;
}

.link-nav-active:hover{
    color:  #c0392b;
}

.btn-login-nav{
   font-size: 16px;
    color:  #c0392b;
    font-weight: 600;
    text-decoration: none;
    border: 2px #c0392b solid;
    border-radius: 50px;
    background-color: white;
    padding-top: 8px;
    padding-bottom: 8px;
    text-align: center;
}

.btn-login-nav:hover{
    color: white;
    background-color: #c0392b;
    text-decoration: none;
}

.btnHam{
    border: 1px #c0392b solid !important;
    background-color: white !important;
    color:#c0392b !important;
}

.btnHam:hover{
    background-color: #c0392b !important;
    color:#fff !important;
}

.link-comm{
    cursor: pointer;
    font-size: 18px;
    color: gray;
}

.comm-img{
    height: 400px;
}

.topic-comm{
     background-color: #f5f6fa;
    border:1px solid #dcdde1;
    width: 95%;
    padding: 5px 12px 5px 12px;
    color: gray;
    border-radius: 10px;
}

.post-comm{
    background-color: #ecf0f1;
    border:none;
    width: 100%;
    height: 100px;
    resize: none;
    color: gray;
}

@media only screen and (max-width:576px){

    .comm-img{
        height: 200px;
    }

    .link-comm{
        font-size: 16px;
    }
    .topic-comm{
        width: 90%;
    }
}
.drop-shadow{
    min-height: 100vh;
    min-width: 100vw;
    background-color: black;
    opacity: 0.5;
    z-index: 9998;   
    position: fixed;
    top: 0;
    left: 0;
}

.t-card{
    min-height: 90vh;
    box-shadow: 1px 2px 8px 2px rgba(0, 0, 0, 0.18), 1px 3px 20px 2px rgba(0, 0, 0, 0.15 );
    background-color: white;
    z-index: 9999;
    margin:0 auto;
    position: absolute;
    left: 0;
    right:0;
    top: 5vh;
}

.hide-scroll::-webkit-scrollbar {
  display: none !important;
}

.hide-scroll {
    -ms-overflow-style: none;
    height: 65vh;
    overflow: auto;
} 

@media only screen and (max-width:992px) {
    .hide-scroll{
        height: 61vh;
    }
}
@media only screen and (max-width: 992px) {
    .media400{
        width:100%;
        min-width: 100%;
        
    }
  }
